@import "../global.scss";

.nav{
    width: 100%;
    height: 50px;
    background-color: $onyx;
    color: $laurel-green;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper{
        width: 100%;
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            display: flex;
            align-items: center;
            .logo{
                font-size: 20px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
            }
        }
        .right{
           .hamburger{
               width: 32px;
               height: 25px;
               display: flex;
               flex-direction: column;
               justify-content: space-between;
               cursor: pointer;
               span{
                   width: 100%;
                   height: 3px;
                   background-color: $laurel-green;
                   transform-origin: left;
                   transition: all 2s ease;
               }
           }
        }
    }
    &.active{
        background-color: $laurel-green;
        color: #5C5C5C;
        .hamburger{
            span{
                &:first-child{
                    background-color: $onyx;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: $onyx;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}
