@import "../../global.scss";

.menu{
    width: 300px;
    height: 100vh;
    background-color: $laurel-green;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    transition: all 1s ease;

    &.active{
        right: 0;
    }
    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 20px;
        font-weight: 300;
        color: $onyx;
        
        li{
            margin-bottom: 20px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }
            &:hover{
                font-weight: 800;
            }
        }
    }
}