.nav{
  width:100%;
  height:50px;
  background-color:#303338;
  color:#bfc7ab;
  position:fixed;
  top:0;
  z-index:3;
  transition:all 1s ease
}

.nav .wrapper{
  width:100%;
  padding:10px 30px;
  display:flex;
  align-items:center;
  justify-content:space-between
}

.nav .wrapper .left{
  display:flex;
  align-items:center
}

.nav .wrapper .left .logo{
  font-size:20px;
  font-weight:700;
  text-decoration:none;
  color:inherit
}

.nav .wrapper .right .hamburger{
  width:32px;
  height:25px;
  display:flex;
  flex-direction:column;
  justify-content:space-between;
  cursor:pointer
}

.nav .wrapper .right .hamburger span{
  width:100%;
  height:3px;
  background-color:#bfc7ab;
  transform-origin:left;
  transition:all 2s ease
}

.nav.active{
  background-color:#bfc7ab;
  color:#5C5C5C
}

.nav.active .hamburger span:first-child{
  background-color:#303338;
  transform:rotate(45deg)
}

.nav.active .hamburger span:nth-child(2){
  opacity:0
}

.nav.active .hamburger span:last-child{
  background-color:#303338;
  transform:rotate(-45deg)
}

