  font-weight:400
}

.home .container .nine h1 span{
  margin-top:5px;
  font-size:15px;
  color:#bfc7ab;
  word-spacing:1px;
  font-weight:normal;
  letter-spacing:2px;
  text-transform:uppercase;
  font-family:"Raleway", sans-serif;
  font-weight:500;
  display:grid;
  grid-template-columns:1fr -webkit-max-content 1fr;
  grid-template-columns:1fr max-content 1fr;
  grid-template-rows:27px 0;
