  height:5px;
  background-color:transparent
}

.home .container .desc{
  font-style:italic
}

.home .container .btnWrapper{
  padding:0;
  list-style-type:none
}

.home .container .btnWrapper .btn{
  font-size:25px;
  width:8em;
  height:2em;
  color:#bfc7ab;
  border-left:0.08em solid;
  position:relative;
  margin-top:0.8em;
  cursor:pointer
}

.home .container .btnWrapper .btn::before,.home .container .btnWrapper .btn::after{
  content:"";
